import React from 'react';
import { string, bool, arrayOf } from 'prop-types';
import cx from 'classnames';

import { Button, Input as ShInput } from '../../../ui';

import styles from './SignupForm.module.scss';

class SignupForm extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      params: []
    };
  }

  componentDidMount(){
    const url = new URL(window.location.href);
    const params = [];

    if (!url.searchParams){
      return;
    }

    for (const key of url.searchParams.keys()){
      params.push({
        name: key,
        value: url.searchParams.get(key)
      });
    }

    this.setState({
      params
    });
  }

  render(){
    const {
      buttonText,
      inputPlaceholder,
      hasAutofocus,
      disabled,
      secondarySubmitBtn,
      splitted
    } = this.props;

    const { params } = this.state;

    const cl = cx({
      [styles.signupForm__container]: true
    });

    return (
      <form
        className={cl}
        action={process.env.API_URL}
        method='post'
      >
        <ShInput
          placeholder={inputPlaceholder}
          name='email'
          type='email'
          required
        />

        {params.map(item => (
          <input
            key={item.name}
            name={item.name}
            value={item.value}
            hidden
            readOnly
          />
        ))}

        <Button type='submit' primary>
          {buttonText}
        </Button>
      </form>
    );
  }
}

SignupForm.propTypes = {
  buttonText: string,
  inputPlaceholder: string,
  hasAutofocus: bool,
  disabled: bool,
  secondarySubmitBtn: bool,
  splitted: arrayOf(string),
};

SignupForm.defaultProps = {
  buttonText: 'Get Started for Free',
  inputPlaceholder: 'Input Email_',
  hasAutofocus: false,
  disabled: false,
  secondarySubmitBtn: false,
  splitted: null
};

export default SignupForm;



