import React from 'react';

import { MainBlock } from './MainBlock';
import { TrustBlock } from '../../components/LandingTrustBlock';

export default ({ data }) => {
  const page = data.markdownRemark;
  const { features, mainBlock, extraBlock } = page.frontmatter;

  return (
    <div>
      <MainBlock params={mainBlock} features={features} />

      <TrustBlock />
    </div>
  );
};

export const query = graphql`
  query LandingV5PageQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        mainBlock {
          title
          buttonText
          bgImage
          inputPlaceholder
        }
        features {
          title
          items {
            icon
            color
            description
          }
        }
      }
    }
  }
`;



